/* src/Chatbot.css */
.chat-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    font-family: Arial, sans-serif;
  }
  
  .chat-container h1 {
    margin-bottom: 20px;
    font-size: 24px;
    color: #333;
  }
  
  .chatbox {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    max-width: 600px;
    height: 100%;
    max-height: 80vh;
    border: 1px solid #ccc;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    overflow: hidden;
  }
  
  .chat-header {
    padding: 10px;
    background-color: #007bff;
    color: #fff;
    text-align: center;
  }
  
  .chat-header h2 {
    margin: 0;
    font-size: 18px;
  }
  
  .chatbox ul {
    list-style: none;
    margin: 0;
    padding: 20px;
    overflow-y: auto;
    flex-grow: 1;
  }
  
  .chatbox .chat {
    display: flex;
    align-items: flex-start;
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 20px;
    max-width: 80%;
    word-wrap: break-word;
  }
  
  .chatbox .chat.incoming {
    background-color: #f1f1f1;
    align-self: flex-start;
    flex-direction: row;
  }
  
  .chatbox .chat.outgoing {
    background-color: #dcf8c6;
    align-self: flex-end;
    flex-direction: row-reverse;
  }
  
  .chatbox .chat .message-content {
    display: inline-block;
    padding: 10px;
    border-radius: 20px;
  }
  
  .chatbox .chat p {
    margin: 0;
  }
  
  .robot-icon {
    width: 30px;
    height: 30px;
    margin-right: 10px;
  }
  
  .chatbox .chat-input {
    display: flex;
    align-items: center;
    padding: 10px;
    border-top: 1px solid #ccc;
    background-color: #fff;
  }
  
  .chatbox .chat-input textarea {
    flex-grow: 1;
    resize: none;
    padding: 10px;
    font-size: 16px;
    border-radius: 20px;
    border: 1px solid #ccc;
    outline: none;
    margin-right: 10px;
  }
  
  .chatbox .chat-input span {
    background-color: #007bff;
    color: #fff;
    padding: 10px 20px;
    border-radius: 20px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .chatbox .chat-input span:hover {
    background-color: #0056b3;
  }
  